@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;600;700;800&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


:root {
  --primary-dark: #5651e5;
  --primary-light: #709dff;
}




@layer base {
  html {
    @apply scroll-smooth
  }

  body {
    @apply font-[Poppins]
  }

  /* li {
    @apply p-4 text-sm
  } */

  /* button {
    @apply px-4 py-2 border bg-gradient-to-r from-[var(--primary-dark)] to-[var(--primary-light)] text-white
  } */

  h2 {
    @apply text-xl font-bold
  }
}